<template>
  <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
        <div slot="right" @click="handleExport" icon="el-icon-plus" style="float: left; margin-right: 10px; color: #03B187; cursor: pointer;"> <SvgIcon icon-class="icondaoru" size="18px" style="float: left;margin: 32px 5px 0 0"></SvgIcon> 导入</div>
      </crumb>
    </div>
    <div class="center_content">
      <div class="center_content_search">
        <div class="center_content_search_left" style="width:100%">
          <div class="search-input-suffix gap" style="display: none">
            <label>培养方案版本：</label>
            <el-select v-model="page.versionId" placeholder="请选择">
              <el-option
                v-for="item in versionOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
          <div class="search-input-suffix gap">
            <label>课程性质：</label>
            <el-select v-model="page.courseProperty" filterable placeholder="请选择">
              <el-option
                v-for="item in fieldCoursePropertyOptions"
                :key="item.code"
                :label="item.value"
                :value="item.code"
              ></el-option>
            </el-select>
          </div>
          <div class="search-input-suffix gap">
            <label>课程类型：</label>
            <el-select v-model="page.classificationId" placeholder="请选择"> 
              <el-option
                v-for="item in courseClassification" 
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
          <!-- <div class="search-input-suffix gap">
                    <label>开课学期：</label>
                     <el-select v-model="page.term" filterable placeholder="请选择">
                        <el-option
                            v-for="item in fieldTermOptions"
                            :key="item.code"
                            :label="item.value"
                            :value="item.code">
                        </el-option>
                    </el-select>
          </div>-->
          <div class="search-input-suffix">
            <label>课程搜索：</label>
            <el-input
              class="search"
              @keyup.enter.native="search"
              v-model="page.keywords"
              placeholder="请输入搜索关键词"
              clearable
            ></el-input>
          </div>
            <div class="search--btn"  @click="getList" style="margin: 0 0 0 20px">搜索</div>
        </div>
      </div>
      <div class="table-container">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column prop="code" label="课程编码" align="left"></el-table-column>
          <el-table-column prop="name" label="课程名称"></el-table-column>
          <el-table-column prop="classificationVal" label="课程类型"></el-table-column>
          <el-table-column prop="credit" label="总学分" align="center" width="80"></el-table-column>
          <el-table-column prop="period" label="总学时" align="center" width="80"></el-table-column>
          <el-table-column prop="termVal" label="开课学期" width="100"></el-table-column>
          <el-table-column prop="leaderName" label="课程负责人" width="120"></el-table-column>
          <el-table-column prop="constructionSituation" align="center" label="课程建设情况"></el-table-column>
          <el-table-column label="操作" width="220">
            <template slot-scope="scope">

              <!-- <SvgIcon icon-class="icondaoru"></SvgIcon>
              <el-upload
                :ref="`uploadFile${scope.row.id}`"
                :show-file-list="false"
                style="display: inline-block; margin-right: 10px"
                class="upload-demo"
                :action="uploadUrl1"
                multiple
                :data="uploadData"
                :limit="1"
                accept=".xls, .xlsx"
                :on-success="handleSuccess1"
                :on-error="onError1"
                name="file"
                :on-exceed="handleExceed1">
                <el-button @click="tmpButtomClick(scope.row.id)" size="small" type="text">点击上传</el-button>
              </el-upload> -->


              <div class="operate-button distance">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>
                <el-button @click.native.prevent="editRow(scope.row)" type="text" size="small">编辑</el-button>
              </div>
              <div class="operate-button distance">
                <SvgIcon icon-class="iconshanchu"></SvgIcon>
                <el-button style="color: #ff5858" @click.native.prevent="deleteRow(scope.row)" type="text" size="small">删除</el-button>
              </div>
              <div class="operate-button">
                <SvgIcon icon-class="iconkechengshezhi"></SvgIcon>
                <el-button @click.native.prevent="setRow(scope.row)" type="text" size="small">课程设置</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div class="center_pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            :current-page="page.page"
            :page-sizes="[15,30,40,50]"
            :page-size="page.rows"
            layout="prev, pager, next, jumper, sizes, total"
            :total="totals"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="userDialogFormVisible"
      :before-close="handleDialogClose"
      class="set-el-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="课程名称" prop="name">
          <el-input v-model="form.name" placeholder></el-input>
        </el-form-item>
        <el-form-item label="培养方案版本" prop="versionId">
          <el-select v-model="form.versionId" placeholder="请选择培养方案版本" disabled>
            <el-option
              v-for="item in versionOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="课程编码" prop="code">
          <el-input v-model="form.code" placeholder></el-input>
        </el-form-item>
        <el-form-item label="课程类型" prop="classificationId">
          <el-select v-model="form.classificationId" filterable placeholder="请选择课程类型">
            <el-option
              v-for="item in courseClassifications"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="课程性质" prop="courseProperty">
          <el-select v-model="form.courseProperty" placeholder="请选择课程性质">
            <el-option
              v-for="item in fieldCoursePropertyOptionsed"
              :key="item.code"
              :label="item.value"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="课程负责人" prop="leader">
          <el-select v-model="form.leader" placeholder="请选择课程负责人" filterable>
            <el-option
              v-for="item in academyOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="学分" prop="credit">
          <el-input-number v-model="form.credit" controls-position="right" :min="0.1" :max="99"></el-input-number>
        </el-form-item>
        <el-form-item label="总学时" prop="period">
          <el-input-number v-model="form.period" controls-position="right" :min="1" :max="9999"></el-input-number>
        </el-form-item>
        <el-form-item label="开课学期" prop="term">
          <el-select v-model="form.term" filterable placeholder="请选择开课学期">
            <el-option
              v-for="item in fieldTermOptions"
              :key="item.code"
              :label="item.value"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')">取 消</el-button>
        <el-button type="primary" @click="create('form')" :disabled="isSubmit">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 导入弹框 -->
    <el-dialog
      title="导入数据"
      :visible.sync="dialogVisible"
      class="set-el-dialog set-course-dialog"
      width="35%"
      :close-on-click-modal="false"
    >
      <div style="height:400px">
        <div style="height: 400px; min-width: 280px; width:50%; float: left">
          <el-steps direction="vertical" :active="active">
            <el-step title="上传文件" icon="el-icon-upload"></el-step>
            <el-step title="检查数据" :icon="icon" description="等待文件上传完毕后自动检查表格数据。"></el-step>
            <el-step title="完成" icon="el-icon-success"></el-step>
          </el-steps>
        </div>
        <div style="position: absolute;top: 105px;left: 85px;">
          <p style="padding: 10px 0;font-size:12px">
            导入数据前，请先
            <el-button type="text" size="mini" @click="download">下载模板</el-button>，按照模板格式录入数据。
          </p>
          <!-- :before-upload="beforeUpload" -->
          <div class="file-upload">
            <el-upload
              ref="uploadXls"
              :disabled="uploadDisabled"
              class="upload-demo"
              :action="uploadUrl"
              :on-success="handleSuccess"
              :on-progress="onProgress"
              :on-error="onError"
              :limit="1"
              :show-file-list="false"
              accept=".xls, .xlsx"
              name="file"
              :on-exceed="handleExceed"
            >
              <el-button size="small" type="primary" @click="handleClick">
                <i class="el-icon-upload el-icon--right"></i> 上传文件
              </el-button>
              <div slot="tip" class="el-upload__tip">支持文件格式：.xls .xlsx。</div>
            </el-upload>
          </div>
        </div>
      </div>
    </el-dialog>
    <!-- 设置课程 -->
    <set-course
      v-model="setCourseDialogFormVisible"
      :course-data="setCourseData"
      @close="closeSetCourseDialog"
      @success="successSetCourseDialog"
    />
  </div>
</template>

<script>
import trainingProgramApi from "@/api/trainingProgramApi";
import SetCourse from "../components/SetCourse.vue";
export default {
  name: "CourseManagement",
  components: { SetCourse },
  data() {
    return {
      uploadData: {
        courseId: ''
      },
      uploadUrl1: '',
      pageTitle: "课程管理",
      icon: 'el-icon-refresh',
      dialogVisible: false,
      uploadDisabled: false,
      uploadUrl: '',
      active: 1,
      totals:0,
      isAdd: true,
      isSubmit:false,
      tableData: [],
      userDialogFormVisible: false,
      setCourseDialogFormVisible: false,
      setCourseData: {},
      versionOptions: [],
      courseClassification: [],
      fieldCoursePropertyOptions: [],
      courseClassifications: [],
      fieldCoursePropertyOptionsed: [],
      fieldTermOptions: [],
      academyOptions: [],
      form: {
        versionId: "",
        code: "",
        classificationId: "",
        name: "",
        courseProperty: "",
        leader: "",
        credit: "",
        period: "",
        term: null,
        id: undefined
      },
      page: {
        versionId: "",
        classificationId: "",
        courseProperty: null,
        keywords: "",
        term: "",
        page: 1,
        rows: 15
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入课程名称",
            trigger: "blur"
          }
        ],
        versionId: [
          {
            required: true,
            message: "请选择培养方案版本",
            trigger: "blur"
          }
        ],
        code: [
          {
            required: true,
            message: "请输入课程编码",
            trigger: "blur"
          }
        ],
        classificationId: [
          {
            required: true,
            message: "请选择课程分类",
            trigger: "blur"
          }
        ],
        courseProperty: [
          {
            required: true,
            message: "请选择课程类型",
            trigger: "blur"
          }
        ],
        leader: [
          {
            required: true,
            message: "请选择课程负责人",
            trigger: "blur"
          }
        ],
        credit: [
          {
            required: true,
            message: "请输入学分",
            trigger: "blur"
          }
        ],
        period: [
          {
            required: true,
            message: "请输入总学时",
            trigger: "blur"
          }
        ],
        term: [
          {
            required: true,
            message: "请输入开课学时",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {},
  mounted() {
    this.getList();
    this.initFn();
  },
  computed: {
    dialogTitle() {
      return this.isAdd ? "添加课程" : "编辑课程";
    }
  },
  methods: {
    initFn() {
      // this.initData()
      // this.getList()
      this.versionAll();
      this.fieldCourseProperty();
      this.courseClassificationAll();
      this.fieldTerm();
      this.getAcademy();
    },
    //获取负责人
   getAcademy() {
      const url = '/basic/teacher/allSysUserId';
      this.$get(url).then(res => {
        this.academyOptions = res.data || [];
      });
    },
    getList() {
      if (localStorage.getItem("versionId")) {
        this.page.versionId = localStorage.getItem("versionId");
        this.initData();
      } else {
        setTimeout(() => {
          this.getList();
        }, 200);
      }
    },

    initData() {
      const url = trainingProgramApi.courseList;
      this.$post(url, this.page).then(res => {
        if (res.status === 1) {
          this.totals = + res.data.total;
          this.tableData = res.data.list;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //培养方案版本
    versionAll() {
      const url = trainingProgramApi.versionAll;
      this.$get(url).then(res => {
        if (res.status === 1) {
          this.versionOptions = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //课程分类
    courseClassificationAll() {
      const url = trainingProgramApi.courseClassificationAll;
      this.$get(url).then(res => {
        if (res.status === 1) {
          const allArr = {
            id: "",
            name: "全部"
          };
          this.courseClassifications = JSON.parse(JSON.stringify(res.data));
          this.courseClassification = res.data;
          this.courseClassification.unshift(allArr);
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //课程性质（课程类型）
    fieldCourseProperty() {
      const url = trainingProgramApi.fieldCourseProperty;
      this.$get(url).then(res => {
        if (res.status === 1) {
          const allArr = {
            code: null,
            value: "全部"
          };

          this.fieldCoursePropertyOptionsed = JSON.parse(
            JSON.stringify(res.data)
          );
          this.fieldCoursePropertyOptions = res.data;
          this.fieldCoursePropertyOptions.unshift(allArr);
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //开课学期
    fieldTerm() {
      const url = trainingProgramApi.fieldTerm;
      this.$get(url).then(res => {
        if (res.status === 1) {
          this.fieldTermOptions = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    headInfo(item) {
      this.pageTitle = item[1];
    },
    search() {
      this.resetTemp();
      this.initData();
    },
    handleSizeChange(val) {
      this.page.rows = val;
      this.initData();
    },
    handleCurrentChange(obj) {
      this.page.page = obj;
      this.initData();
    },
    openAdd() {
      this.isAdd = true;
      this.isSubmit=false;
      this.userDialogFormVisible = true;
      this.$nextTick(() => {
        this.form = {
        versionId: "",
        code: "",
        classificationId: "",
        name: "",
        courseProperty: "",
        leader: "",
        credit: "",
        period: "",
        term: null,
        id: undefined
      };
      this.form.versionId = localStorage.getItem("versionId");
        this.$refs["form"].resetFields();
      });
    },
    deleteRow(row) {
      this.$confirm("确定删除该课程?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.userDel(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    editRow(row) {
      const obj = {
        versionId: row.versionId,
        code: row.code,
        classificationId: row.classificationId,
        name: row.name,
        courseProperty: row.courseProperty,
        leader: row.leader,
        credit: row.credit,
        period: row.period,
        term: row.term,
        id: row.id
      };
      this.form.versionId = localStorage.getItem("versionId");
      this.form = obj;
      this.userDialogFormVisible = true;
      this.isAdd = false;
      this.isSubmit=false;
    },
    setRow(row) {
      this.setCourseData = row;
      this.setCourseDialogFormVisible = true;
    },
    closeSetCourseDialog(val) {
      this.setCourseDialogFormVisible = val;
    },
    successSetCourseDialog(val) {
      this.courseSet(val);
    },
    create(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.isSubmit=true;
          if (this.isAdd) {
            this.userAdd();
          } else {
            this.userEdit();
          }
        }
      });
    },
    setCourseCreate(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
        }
      });
    },
    cancel(formName) {
      this.userDialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    handleDialogClose() {
      this.$refs["form"].resetFields();
      this.userDialogFormVisible = false;
    },
    setCourseCancel(formName) {
      this.setCourseDialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    userAdd() {
      const url = trainingProgramApi.courseAdd;
      this.$post(url, this.form)
        .then(res => {
          if (res.status === 1) {
              this.isSubmit=true;
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.resetTemp();
            this.initData();
          } else {
             this.isSubmit=false;
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
           this.isSubmit=false;
          console.log(err);
        });
    },
    userDel(id) {
      const url = trainingProgramApi.courseDel({
        id: id
      });
      this.$deletes(url)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userEdit() {
      const url = trainingProgramApi.courseEdit;
      this.$put(url, this.form)
        .then(res => {
          if (res.status === 1) {
              this.isSubmit=true;
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.initData();
          } else {
            this.isSubmit=false;
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          this.isSubmit=false;
          console.log(err);
        });
    },
    courseSet(formData) {
      const url = trainingProgramApi.courseSet;
      this.$put(url, formData)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    resetTemp() {
      this.page.page = 1;
      this.page.rows = 15;
    },

    handleClick() {
      this.uploadDisabled = false;
    },
    handleExport() {
      this.active = 1;
      this.dialogVisible = true;
      let token = localStorage.getItem("x_token");
      this.uploadUrl =
        this.API_URL +
        "/basic/import/courseInformation" +
        `?x-token=${token}&versionId=${localStorage.getItem('versionId')}`;
      this.$nextTick(() => {
        this.$refs.uploadXls.clearFiles();
      });
    },
    handleExceed(files, fileList) {
      this.$notify.warning({
        title: "提示",
        message: `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      });
    },
    onProgress() {
      this.active = 2;
      this.uploadDisabled = true;
    },
    handleSuccess(res) {
      if (res.status === 1) {
        this.$notify.success({
          title: "提示",
          message: "导入成功!"
        });
        this.active = 3;
        this.uploadDisabled = false;
        this.getList();
      } else {
        if (res.data) {
          this.$notify.error({
            title: "提示",
            message: "导入失败!" + res.data[0].message
          });
        } else {
          this.$notify.error({
            title: "提示",
            message: res.message
          });
        }
        this.active = 1;
        this.uploadDisabled = false;
      }
      this.$nextTick(() => {
        this.$refs.uploadXls.clearFiles();
      });
    },
    download() {
      location.href =  this.API_URL + '/basic/import/template/courseInformation'
    },
    onError() {
      this.$notify.error({
        title: "提示",
        message: "上传失败!请尝试重新上传!"
      });
      this.active = 1;
      this.uploadDisabled = false;
      this.$nextTick(() => {
        this.$refs.uploadXls.clearFiles();
      });
    },


    tmpButtomClick(id) {
      this.uploadData.courseId = id
      let token = localStorage.getItem("x_token");
      this.uploadUrl1 = 
        this.API_URL +
        "/teacher/batch/course/batchUpload" +
        `?x-token=${token}`;
      this.$nextTick(() => {
        this.$refs[`uploadFile${id}`].clearFiles();
      });
    },
    handleSuccess1(res) {
      console.log(res)
      if (res.code === 200) {
        this.$notify.success({
          title: "提示",
          message: "导入成功!"
        })
      } else {
        this.$notify.error({
          title: "提示",
          message: "导入失败!"
        })
        // if (res.data) {
        //   this.$notify.error({
        //     title: "提示",
        //     message: "导入失败!" + res.data[0].message
        //   });
        // } else {
        //   this.$notify.error({
        //     title: "提示",
        //     message: res.message
        //   });
        // }
      }
      this.$nextTick(() => {
        this.$refs[`uploadFile${this.uploadData.courseId}`].clearFiles()
      });
    },
    onError1() {
      this.$notify.error({
        title: "提示",
        message: "上传失败!请尝试重新上传!"
      });
      this.$nextTick(() => {
        this.$refs[`uploadFile${this.uploadData.courseId}`].clearFiles()
      });
    },
    handleExceed1(files, fileList) {
      this.$notify.warning({
        title: "提示",
        message: `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      });
    },
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
</style>



